@import 'src/common/components/sass/variables.scss';


.project {
  width: 450px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  box-sizing: border-box;
  padding: 10px;

  .ProjectImgContainer {
    height: 200px;
    background-color: #d4bf38;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    border-radius: 5px 5px 0 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;

    .projectBottom {
      width: 100px;
      height: 30px;
      border: 1px solid black;
      background-color: $bgColor;
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      color: $textColor;
      opacity: 0.8;
      position: relative;
    }

    &::before {
      position: absolute;
      content: "";
      background-color: $bgSecondColor;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.2;
    }
  }

  .descriptionBlock {
    width: 100%;
    border: 1px solid;
    text-align: start;
    box-sizing: border-box;
    background-color: $bgSecondColor;
    padding: 20px 25px 25px;
    border-radius: 0 0 5px 5px;


    .titleProject {
      font-size: 20px;
      font-weight: 600;
      font-family: "Poppins", sans-serif;
      color: $primaryColor;
    }

    .textProject {
      background-color: $bgSecondColor;
      color: $textColor;
    }
  }
}










